<template>
	<div id="article-details">
		<common-header :title="title"></common-header>
		<div class="content" v-html="article.content">
		</div>
	</div>
</template>

<script>
	
export default {
	data(){
		return {
			title:'',
			article:{}
		}
	},
	created(){
		this.getArticleDetails()
	},
  components: {
  	
  },
  methods:{
  	getArticleDetails(){
  		let id = this.$route.query.id
  		let type = this.$route.query.type
  		this.$Api.getArticleDetails(id).then((res)=>{
  			if(res.q.s == 0){
  				if(type == 'article'){
  					this.title = '文章详情'
  				}else if(type == 'notice'){
  					this.title = '系统公告'
  				}else{
  					this.title = '规则说明'
  				}
  				this.article = res.q.article
  			}
  		})
  	}
  }
  
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#article-details{
		padding: 0.4rem;
		font-size: 0.4rem;
		color: #333;
	}
</style>


